<template>
  <button @click="goBack">Back</button>
  <div>
    <label>
      <input type="checkbox" v-model="showLeft" />
      {{ showLeft ? 'Nascondi sinistra' : 'Mostra sinistra' }}
    </label>
    <label>
      <input type="checkbox" v-model="showRight" />
      {{ showRight ? 'Nascondi destra' : 'Mostra destra' }}
    </label>
  </div>
  <div style="position: relative; display: inline-block;">
    <video
      ref="video"
      :src="videoUrl"
      controls
      style="max-width: 100%; max-height: 50vh;"
    ></video>
    <canvas
      ref="canvas"
      style="position: absolute; left: 0; top: 0; pointer-events: none;"
    ></canvas>
  </div>
</template>

<script>
export default {
  name: 'MediaPipeResultsView',
  data() {
    return {
      results: history.state?.results || [],
      videoUrl: history.state?.videoUrl || null,
      currentFrame: null,
      animationFrameId: null,
      showLeft: true,
      showRight: true,
    }
  },
  mounted() {
    this.$refs.video.addEventListener('loadedmetadata', this.resizeCanvas);
    window.addEventListener('resize', this.resizeCanvas);

    // Avvia/ferma il loop di disegno in base allo stato del video
    this.$refs.video.addEventListener('play', this.startDrawing);
    this.$refs.video.addEventListener('pause', this.stopDrawing);
    this.$refs.video.addEventListener('ended', this.stopDrawing);

    // Aggiorna i puntini anche quando il tempo cambia (ad esempio in pausa)
    this.$refs.video.addEventListener('timeupdate', this.drawLandmarks);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.resizeCanvas);
    this.stopDrawing();
    this.$refs.video.removeEventListener('timeupdate', this.drawLandmarks);
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    resizeCanvas() {
      const video = this.$refs.video;
      const canvas = this.$refs.canvas;
      // Imposta il canvas alle dimensioni visibili del video
      canvas.width = video.clientWidth;
      canvas.height = video.clientHeight;
      canvas.style.width = video.clientWidth + 'px';
      canvas.style.height = video.clientHeight + 'px';
    },
    startDrawing() {
      if (this.animationFrameId) return;
      const draw = () => {
        this.drawLandmarks();
        this.animationFrameId = requestAnimationFrame(draw);
      };
      draw();
    },
    stopDrawing() {
      if (this.animationFrameId) {
        cancelAnimationFrame(this.animationFrameId);
        this.animationFrameId = null;
      }
    },
    drawLandmarks() {
      const video = this.$refs.video;
      const canvas = this.$refs.canvas;
      if (!canvas || !video) return;
      this.resizeCanvas();

      // Trova il frame più vicino al tempo attuale
      const t = video.currentTime;
      let closest = null;
      let minDiff = Infinity;
      for (const r of this.results) {
        const diff = Math.abs(r.time - t);
        if (diff < minDiff) {
          minDiff = diff;
          closest = r;
        }
      }
      const ctx = canvas.getContext('2d');
      ctx.clearRect(0, 0, canvas.width, canvas.height);

      if (!closest || !closest.poses?.landmarks?.length) {
        return;
      }

      ctx.fillStyle = 'red';
      const width = canvas.width;
      const height = canvas.height;

      // Indici dei landmark sinistra e destra secondo MediaPipe
      const leftIndices = [
        11, 13, 15, 17, 19, 21, // braccio sinistro
        23, 25, 27, 29, 31 // gamba sinistra
      ];
      const rightIndices = [
        12, 14, 16, 18, 20, 22, // braccio destro
        24, 26, 28, 30, 32 // gamba destra
      ];

      for (const pose of closest.poses.landmarks) {
        pose.forEach((lm, idx) => {
          if (
            (this.showLeft && leftIndices.includes(idx)) ||
            (this.showRight && rightIndices.includes(idx))
          ) {
            const x = lm.x * width;
            const y = lm.y * height;
            ctx.beginPath();
            ctx.arc(x, y, 4, 0, 2 * Math.PI);
            ctx.fill();
          }
        });
      }
    }
  }
}
</script>

