<template>
  <div class="selected-video-view">
    <label>Model:
      <select v-model="selectedModel">
        <option value="pose_landmarker_full.task">Full</option>
        <option value="pose_landmarker_heavy.task">Heavy</option>
        <option value="pose_landmarker_lite.task">Lite</option>
      </select>
    </label>
    <video
        v-if="videoUrl"
        :src="videoUrl"
        controls
        autoplay
        loop
        :key="videoUrl"
    ></video>
    <div v-if="analyzing" class="progress-container">
      <label>Analisi in corso: {{ analysisProgress }}%</label>
      <progress :value="analysisProgress" max="100"></progress>
    </div>
    <div class="navigation-buttons">
      <button @click="goBack">Back</button>
      <button @click="triggerVideoSelection">Change Video</button>
      <button @click="analyzeAndGoToResults">Analyze</button>
    </div>
    <input
      ref="videoInput"
      type="file"
      accept="video/*"
      style="display: none"
      @change="handleVideoSelected">
  </div>
</template>

<script>
import { PoseLandmarker, FilesetResolver } from '@mediapipe/tasks-vision';

export default {
  name: 'SelectedVideoView',
  data() {
    return {
      videoUrl: this.$route.query.videoUrl || null,
      analyzing: false,
      analysisProgress: 0,
      selectedModel: 'pose_landmarker_heavy.task', // Modello Default
      landmarkerInstance: null,
      videoElement: null,
    }
  },
  methods: {
    triggerVideoSelection() {
      // Reset input prima di aprire il selettore
      if (this.$refs.videoInput) {
        this.$refs.videoInput.value = '';
        this.$nextTick(() => {
          this.$refs.videoInput.click();
        });
      }
    },
    handleVideoSelected(event) {
      // Non serve più resettare qui, lo fai prima di aprire il selettore
      const file = event.target.files[0];
      const allowedFormats = ['video/mp4', 'video/webm', 'video/ogg'];
      if (file) {
        if (!allowedFormats.includes(file.type)) {
          alert(
            `Unsupported video format: ${file.type || 'unknown'}.\n` +
            `Supported formats: ${allowedFormats.join(', ')}`
          );
          return;
        }
        // Rilascia video precedente
        if (this.videoUrl) {
          URL.revokeObjectURL(this.videoUrl);
        }
        const videoUrl = URL.createObjectURL(file);
        this.videoUrl = videoUrl;
      }
    },
    goBack() {
      this.$router.go(-1);
    },
    async analyzeAndGoToResults() {
      if (!this.videoUrl) {
        alert('Select a video first.');
        return;
      }
      // Controllo connessione
      try {
        await fetch('https://www.google.com', { mode: 'no-cors' });
      } catch (e) {
        alert('You are offline');
        return;
      }
      this.analyzing = true;
      const results = await this.analyzeVideo(this.videoUrl);
      this.analyzing = false;
      this.$router.push({
        name: 'analysis-results',
        state: { results, videoUrl: this.videoUrl }
      });
    },
    async analyzeVideo(videoUrl) {
      // Carica modello
      const vision = await FilesetResolver.forVisionTasks(
        'https://cdn.jsdelivr.net/npm/@mediapipe/tasks-vision@latest/wasm'
      );
      const landmarker = await PoseLandmarker.createFromOptions(vision, {
        baseOptions: {
          modelAssetPath: `/models/${this.selectedModel}`,
          delegate: 'GPU'
        },
        runningMode: 'VIDEO',
        numPoses: 1,
      });
      this.landmarkerInstance = landmarker; // salva riferimento

      return new Promise((resolve) => {
        const video = document.createElement('video');
        video.src = videoUrl;
        video.crossOrigin = 'anonymous';
        video.muted = true;
        video.playsInline = true;
        video.autoplay = false;
        video.loop = false;
        video.style.display = 'none';
        document.body.appendChild(video);
        this.videoElement = video; // salva riferimento

        const results = [];
        const fps = 10;
        video.onloadedmetadata = async () => {
          video.currentTime = 0;
          video.play();
          const duration = video.duration;
          let currentTime = 0;

          const processFrame = async () => {
            if (currentTime > duration) {
              video.pause();
              document.body.removeChild(video);
              resolve(results);
              return;
            }
            video.currentTime = currentTime;
            await new Promise(res => video.onseeked = res);
            const poses = landmarker.detectForVideo(video, currentTime * 1000);
            results.push({ time: currentTime, poses });
            // Logga il risultato di ogni frame
            console.log(`Frame @ ${currentTime.toFixed(2)}s:`, poses);
            console.log("Using model:", this.selectedModel);
            this.analysisProgress = Math.min(100, Math.round((currentTime / duration) * 100));
            currentTime += 1 / fps;
            setTimeout(processFrame, 0);
          };
          processFrame();
        };
      });
    }
  },
  beforeUnmount() {
    // Rilascia modello
    if (this.landmarkerInstance && this.landmarkerInstance.close) {
      this.landmarkerInstance.close();
      this.landmarkerInstance = null;
    }
    // Rimuovi video creato per l'analisi
    if (this.videoElement && this.videoElement.parentNode) {
      this.videoElement.pause();
      this.videoElement.parentNode.removeChild(this.videoElement);
      this.videoElement = null;
    }
    // Rimuovi video dal template
    this.videoUrl = null;
  }
}
</script>

<style scoped>
video {
    max-width: 100%;
    max-height: 50vh;
    object-fit: contain;
}
.selected-video-view {
  display: flex;
  flex-direction: column;
}
.progress-container {
  margin: 1em 0;
}
progress {
  width: 100%;
  height: 20px;
}
</style>
