<template>
  <div class="home">
    <button @click="triggerCamera">Record Video</button>
    <button @click="triggerVideoSelection">Analyze Video</button>
    <button>Real Time Analyzer</button>
    <button>Track</button>
    <input
      ref="cameraInput"
      type="file"
      accept="video/*"
      capture="environment"
      style="display: none"
      @change="handleVideoSelected">
    <input
      ref="videoInput"
      type="file"
      accept="video/*"
      style="display: none"
      @change="handleVideoSelected">
  </div>
</template>

<script>
// @ is an alias to /src


export default {
  name: 'HomeView',
  components: {
    // HelloWorld
  },
  methods: {
    triggerCamera() {
      this.$refs.cameraInput.click();
    },
    triggerVideoSelection() {
      this.$refs.videoInput.click();
    },
    handleVideoSelected(event) {
      const input = event.target;
      const file = input.files[0];
      const allowedFormats = ['video/mp4', 'video/webm', 'video/ogg'];
      // Reset per permettere nuova selezione
      input.value = '';
      if (file) {
        if (!allowedFormats.includes(file.type)) {
          alert(
            `Formato non supportato: ${file.type || 'sconosciuto'}.\n` +
            `Formati consentiti: ${allowedFormats.join(', ')}`
          );
          return;
        }
        const videoUrl = URL.createObjectURL(file);
        this.$router.push({
          name: 'selected-video',
          query: { videoUrl }
        });
      }
    }
  },
  mounted() {
    if (this.$refs.videoInput) {
      this.$refs.videoInput.value = ''; // Reset input video
    }
  }
}
</script>

<style scoped>
.home {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.home button {
  width: 200px;
  margin-bottom: 10px;
}
</style>
